.NavPanel {
  position: absolute;
  top: calc(100vh);
  left: 10%;
  -webkit-transform: translate(-50%, 50%);
      -ms-transform: translate(-50%, 50%);
          transform: translate(-50%, 50%);
  z-index: 9999;
  background: #fff;
  text-align: left;
  padding: 0.25em;
  margin: 0;
  background: grey;
  -webkit-box-shadow: 4px 10px 10px rgb(103, 102, 102);
          box-shadow: 4px 10px 10px rgb(103, 102, 102);
}

.FloatingNavPanel {
  top: 0%;
  -webkit-transform: translate(-50%, 50%);
      -ms-transform: translate(-50%, 50%);
          transform: translate(-50%, 50%);
  position: fixed;
}

.NavPanel ul {
  padding: 0;
  margin: 0 0 0 0;
  background: black;
}

.NavPanel li {
  list-style-type: none;
  margin-left: 0px;
  line-height: 200%;
  color: black;
  background: rgb(74, 138, 255);
  padding-left: 2px;
  padding-right: 2px;
}
.NavPanel li:active {
  background-color: black;
  color: white;
}
.NavPanel li:hover {
    background-color: rgb(2, 19, 255);
}

.NavPanel li:nth-child(odd) {
  background-color: #fff;
}

.NavPanel li:active:nth-child(odd){
    background-color: black;
    color: white;
}
.NavPanel li:hover:nth-child(odd){
    background-color: #0aa7f0;
}



