.ProjectsPanel {
  height: 100vh;
  width: 100vw;
  text-align: center;
  position: relative;
  display:table;
}
.ProjectsPanelBlurb {
  width: 120%;
  height: 100%;
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#427687b4), to(#42768775));
  background-image: -webkit-linear-gradient(#427687b4, #42768775);
  background-image: -o-linear-gradient(#427687b4, #42768775);
  background-image: linear-gradient(#427687b4, #42768775);
}
#ProjectsContainer {
  width: calc(80% - 10em); /* 10 em to represent the widest navigation item's number of characters */
  height: 80%;
  text-align: left;
  border-style: solid;
  border-color: gray;
  border-width: 1px;
  background-color: #ffffff;
  margin: auto;
  padding: 1%;
  overflow: scroll;
}


