.WelcomePanel {
  height: 100vh;
  width: 100vw;
  text-align: center;
  position: relative;
  display:table;
}
.WelcomePanelBlurb {
  width: 100%;
  height: 100%;
  display:table-cell;
  vertical-align: middle;
  color: white;
}

.WelcomePanel::after {
  content: "";
  background-image: url('./res/scuba-diver-grotto.jpg');
  background-color: transparent;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.5;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1;
}

body { background: #2d2d37;}
.arrow span { color: white; text-decoration: none; }

.arrow {
  text-align: center;
  margin: 8% 0;
  left: 50%;
  bottom: -60px;
  position: absolute;
}
.bounce {
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite;
}

@-webkit-keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
  }
  60% {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
  }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
  }
  60% {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
  }
}



